<script>
import { mapState, mapActions } from "vuex"
import { format } from 'date-fns'

export default {
  data: () => ({
    today: format(new Date(), 'yyyy-MM-dd')
  }),
  computed: {
    ...mapState("dashboard", ["records", "recordsLoading"]),
  },
  created() {
    this.fetchData()
  },
  methods: {
    ...mapActions("dashboard", ["getRecords"]),
    clickTodays(type) {
      if (type=='todays_applicant') {
        this.$router.push({ name: 'Applicants', query: { created_at_start: this.today, created_at_end: this.today } })
      }

      // Dito ako natapos sa mga link na ito

      if (type=='todays_backended') {
        this.$router.push({ name: 'Applicants', query: { status_updated_at_start: this.today, status_updated_at_end: this.today, status: 3 } })
      }

      if (type=='todays_qualified') {
        this.$router.push({ name: 'Applicants', query: { status_updated_at_start: this.today, status_updated_at_end: this.today, status: 4 } })
      }

      if (type=='for_interview') {
        this.$router.push({ name: 'Applicants', query: { status: 2 } })
      }
    },
    async fetchData() {
      if (!this.recordsLoading) {
        this.$store.commit("dashboard/setRecords", [])
        this.getRecords()
      }
    }
  }
}
</script>

<style scoped>
.cursor-pointer {
  cursor: pointer
